﻿$fade-time: 18s;

@media (max-width: 768px) {
  .slider div {
    background-size: contain;
  }

  .slider img {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .slider div {
    background-size: cover;
  }

  .slider img {
    display: block;
    margin: 0 auto;
    max-width: 100%;
    max-height: 600px;
  }
}

.slider {
  max-width: 100%;
  max-height: 650px;
  position: relative;
  margin-bottom: 20px;
}

.slide2,
.slide3,
.slide4,
.slide5 {
  top: 0px;
  left: 0px;
  position: absolute;
  width: 100%;
  height: 100%;
}

.slider div {
  animation-delay: 2s;
  -webkit-animation-delay: 2s;
}

.slide1 {
  z-index: 10;
  animation: fade $fade-time infinite;
  -webkit-animation: fade $fade-time infinite;
}

.slide2 {
  z-index: 0;
  animation: fade2 $fade-time infinite;
  -webkit-animation: fade2 $fade-time infinite;
}

.slide3 {
  z-index: 0;
  animation: fade3 $fade-time infinite;
  -webkit-animation: fade3 $fade-time infinite;
}

@keyframes fade {
  0% {
    opacity: 1;
  }

  33.333% {
    opacity: 0;
  }

  66.666% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fade2 {
  0% {
    opacity: 0;
  }

  33.333% {
    opacity: 1;
  }

  66.666% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fade3 {
  0% {
    opacity: 0;
  }

  33.333% {
    opacity: 0;
  }

  66.666% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
